import { j as json } from "../../../../chunks/index.js";
import { C as CatProduct } from "../../../../chunks/catProduct.js";
async function GET() {
  try {
    const cats = await CatProduct.findAll({
      order: [["name", "ASC"]],
      attributes: { exclude: ["createdAt", "updatedAt"] }
    });
    return json(cats);
  } catch (error) {
    console.error("get cats error", error);
    return { status: 500 };
  }
}
export {
  GET
};
